import React from 'react';
import {
  HeaderContainer,
  HeaderTitle,
  Navigation,
  NavLink,
} from './Header.style';

function Header() {
  return (
    <HeaderContainer>
      <HeaderTitle>React todo</HeaderTitle>
      <Navigation>
        <NavLink to="/react-todo">Главная</NavLink>
        <NavLink to="/react-todo/todo-list">Список todo</NavLink>
      </Navigation>
    </HeaderContainer>
  );
}

export default Header;
